html{
  font-size: $html-font-size-lg;
}

body {
  width: 100%;
  height: 100%;
  // font-family: 'Oxygen', sans-serif;
  //font-family: 'Slabo 27px', serif;
  font-family: $default-font-family;
  letter-spacing: 0.6px;
  line-height: 1.8rem;
  position: relative;
  margin: 0;
  overflow: hidden;
  &.loaded{
    overflow: auto;
  }
  -webkit-font-smoothing: antialiased;
  text-shadow: rgba(0,0,0,.01) 0 0 1px;
  background: url("../../img/small_steps.png");
}

h1,h2,h3,h4,h5,h6{
  font-family: 'Montserrat', sans-serif;
}

p{
  color: $color-grey;
  font-size: 1rem;
  font-weight: 400;
}


.angle{
  @include angle(after);
}

section{
  position: relative;
  padding: 4rem 0%;
  .header-section{
    position: relative;
    margin-top: 4rem;
    margin-bottom: 4rem;
    text-align: center;
    .line{
      display: none;
      position: absolute;
      left: 15%;
      top: 20%;
      //margin: 0 auto;
      background-color: $color-primary;
      border: none;
      //border: 1px solid darken($color-light-grey, 2%);
      height: .1rem;
      width: 7rem;
      opacity: .5;
      @include media-breakpoint-down(sm) {
        width: 2rem;
        top: 0;
      }

    }
    h1,h2{
      position: relative;
      font-size: 2.827rem;
      padding-bottom: 1rem;
      opacity: .95;

      margin-bottom: 0;
      color: $color-dark;
      opacity: .8;
      z-index: 10;
      font-weight: 700;

    }
    h2.primary {
      display: inline-block;

      &:after {
        content: "";
        width: 120%;
        margin: 0 auto;
        display: block;
        position: relative;
        top: -1rem;
        left: -10%;
        height: 1rem;
        background-color: $color-primary;
        opacity: .7;
        z-index: -1;
        clip-path: polygon(10% 0%, 100% 0%, 90% 100%, 0% 100%);
      }
    }

    h3.secondary{
      font-size: 3rem;
      color: $color-grey;
      position: relative;
      text-align: center;
      display: none;
      left: 0;
      margin: 0 auto;
      top: -4rem;
      z-index: 10;
    }
  }

}

.btn{
  @include transition(all,$standart-animaion-time);
}
.btn.btn-primary{
  color: white ;
 // border-radius: 30px;
  font-weight: 300;
  border: 1px solid $color-danger;
  background-color: transparent;
  border-radius: 0;
  padding: 0.8rem 3rem;
  // background-color: rgba($color-danger,1) ;
  //background-color: lighten($color-danger, 0%);
  position: relative;

  &:hover{
    background-color: $color-danger;
  }
  &:before{
    //border: $color-danger solid 6px;
    //border-radius: 30px;
  }
}
svg {
    max-width: 100%;
}
.animate {
  visibility: hidden;
  opacity: 0;
}
.animated {
  visibility: visible;
  opacity: 1;
  animation-duration: 1s;
}

.vertical-line {
    position: absolute;
    height: 100%;
    width: 1px;
    top: 0;
    left: 85vw;
    //background-color: $color-primary;
    border-right: 1px dashed $color-primary;
    z-index: -1;
}
.geometry-bkgrnd {
    //display: none;
    //width: 100%;
    //height: 100vh;
    position: absolute;
    bottom: 0;
    height: 100%;
    @include flexbox;
    @include align-content(space-between);
    svg {
        width: 100%;
        z-index: 10;
        
    }
    
    div[class*="lvl"] {
        position: absolute;
        @include transform(translateY(-50%));
    }
    
    .lvl-1 {
        width: 5rem;
        opacity: .9;
    }
    .lvl-2 {
        width: 4rem;
        opacity: .7;
    }
    .lvl-3 {
        width: 3rem;
        opacity: .5;
    }
    
    
    .home {
        width: 3rem;
        left: 20vw;
        bottom: 80%;
    }
    .oo {
        bottom: 40%;
        left: 70vw;
        @include transform(rotate(10deg));
    }
    .triangle {
        left: 15vw;
        bottom: 60%;
    }

    .rectangle {
        bottom: 70%;
        left: 20vw;
    }
    .star{
        bottom: 50%;
        left: 80vw;
    }
    .polygon_3 {
        bottom: 73%;
        left: calc(85vw - 2.5rem);
        
    }
    .rectangle_4 {
        bottom: -5%;
        left: 60vw;
    }
    .polygon_2 {
        bottom: 20%;
        left: 65vw;
    }

}

.finn-and-jake {
    width: 30vw;
    position: fixed;
    bottom: -1rem;
    right: 5%;
    //display: none;
    visibility: hidden;
    @include transform(translateY(100%));
    transition: all 1s ease-out;
    &.is-shown {
        //display: block;
        visibility: visible;
        @include transform(translateY(0%));
    }
}

.tooltip-inner {
    border-radius: 0;
}



