$text-color: $color-light-grey;

#contact
{

	background: url("../../img/galaxy.jpg");
	background-repeat: no-repeat;
	background-size: cover;

	position: relative;


	&:after
	{
		//content: "";
		display: block;
		width: 100%;
		height: 100%;
		background: rgba($color-secondary,.9);
		position: absolute;
		top: 0;
		left: 0;
	}
	h2
	{
		color: $color-primary;
		opacity: 1;
		//podtrzeni nadpisu
		&:after
		{
			background-color: $color-danger;
			@include media-breakpoint-down(sm)
			{
				width: 100%;
				left: 0;
			}
		}
	}

	.card
	{
		margin: 0 auto;
		background-color: rgba(255,255,255,.2);
		background-color: rgba(0,0,0,.7);
		padding: 2rem;
		//box-shadow:  0px 2px 10px 0px rgba(255,255,255,255.16);
	}
	.content
	{
		position: relative;
		z-index: 1;
		@include media-breakpoint-down(sm)
		{
			padding: 0;
		}
	}
	.form
	{
		background: rgba($color-secondary, .85);
		padding: 3rem;

		@include media-breakpoint-down(sm)
		{
			padding: 1.5rem;
		}

		.form-group
		{
			position: relative;
			//padding-top: 1rem;
			label
			{
				font-weight: 300;
				opacity: .7;
				font-size: 1.4rem;
				transition: .3s all ease-out;
				color: $text-color;
				@include transform(translateY(140%));
				&.focused
				{
					font-size: 1rem;
					color: #7AAEF2;
					@include transform(translateY(60%));
				}
			}



			.line
			{
				height: 1px;
				background-color: $color-primary;
				opacity: .4;
				margin: 0 auto;
				//width: 100%;
				// margin-left: 1.9rem ;
			}
		}
		.form-control
		{
			border: none;
		}
	}

	.line
	{
		height: 1px;
		background-color: $color-primary;
		opacity: .4;
		margin: 0 auto;
		//width: 100%;
		// margin-left: 1.9rem ;
	}
	input[type="text"], textarea
	{
		background-color: transparent;
		color: $color-secondary;
		// letter-spacing: 1px;
		// margin-left: 1rem;
		font-weight: 300;
		//font-style: italic;
		padding-bottom: .2rem;
		border: transparent;
		@include transition(all,.3s);
		@include placeholder
		{
			color: #7f8c8d;
		}
		@include media-breakpoint-down(sm)
		{
			font-size: 1.1rem;
		}
		&:focus
		{
			outline: none;
			border-color: transparent;
			// border-color: darken($color-primary,10%);
			//border-width: 2px;
		}
		&:focus + .line
		{
			opacity: 1;
		}
	}



	.contact-card
	{
		position: relative;
		background-color: white;
		height: 30rem;
		overflow: hidden;
		@include shadow-1;
		@include media-breakpoint-down(sm)
		{
			height: auto;
		}
		.mail-icon
		{
			position: absolute;
			left: -20%;
			height: 65%;
			top: 50%;
			@include transform(translateY(-50%));
			@include media-breakpoint-down(sm)
			{
				display: none;
			}
		}
		.background, .background-mobile
		{
			position: absolute;
			right: 0;
			height: 100%;
			top: 0;
		}
		.background-mobile{
			display: none;
		}
		@include media-breakpoint-down(sm)
		{
			.background
			{
				display: none;
			}
			.background-mobile {
				display: block;
			}
		}
		> .row
		{
			height: 100%;
		}
		.left, .right
		{
			padding: 3.2rem 4rem;
			@include media-breakpoint-down(sm)
			{
				padding: 3rem 2rem;
			}
		}
		.left
		{
			font-family: $handwrited-font-family;
			font-size: 1.3rem;
			@include media-breakpoint-down(sm)
			{
				//order: 2;
				height: auto;
				padding-bottom: 0;
			}
			h3
			{
				font-family: inherit;
				font-weight: bold;
				margin-bottom: 2rem;
			}
			.form-control
			{
				font-family: $default-font-family;
			}
			position: relative;
			height: 100%;
			&:after
			{
				content: "";
				position: absolute;
				right: 0;
				width: 1px;
				height: 80%;
				top: 50%;
				@include transform(translateY(-50%));
				background-color: rgba($color-dark, .2);
			}
			.label
			{
				margin: 0;
			}
			.nameInput
			{
				display: inline-block;

				.form-control
				{
					margin: 0;
					width: 100%;
				}
			}
			.submitGroup
			{
				text-align: right;
				position: absolute;
				bottom: 2rem;
				right: 2rem;
				@include media-breakpoint-down(sm)
				{
					text-align: left;
					left: 2rem;
					position: relative;
					top: 0;
				}
			}
			.btn
			{
				border-radius: 100%;
				background-color: $color-danger;
				color: white;
				font-size: 1.5rem;
				height: 3.5rem;
				width: 3.5rem;
				font-family: $default-font-family;
				outline: none;
				.text
				{
					visibility: hidden;
					display: none;
				}
				transition: all .5s ease-in-out;
				svg
				{
					width: 1.5rem;
					margin-left: -.2rem;
					margin-top: .4rem;
					path
					{
						transition: inherit;
					}
				}
				border: 2px solid transparent;


				&:hover
				{
					//width: auto;
					//border-radius: 25px;
					.text
					{
						//display: block;
						//visibility: visible;
					}
					border: 2px solid $color-danger;
					background: white;
					svg path
					{
						fill: $color-danger;
					}
				}
			}
		}
		.right
		{
			position: relative;
			@include media-breakpoint-down(sm)
			{
				//order: 1;
				padding-top: 0;
			}
			.contact-photo
			{
				width: 40%;
				margin: 0 auto;
				display: block;
				@include media-breakpoint-down(sm)
				{
					margin: 0;
					display: none;
				}
			}
			.contacts
			{
				text-align: right;
				position: absolute;
				right: 3rem;
				bottom: 6rem;

				p
				{
					font-size: 1.1rem;
					color: white;
					margin: .7rem;
				}
				@include media-breakpoint-down(sm)
				{
					position: relative;
					top: 0;
					right: 0;
				}
			}
			.phone-icon
			{
				position: absolute;
				bottom: 6rem;
				right: 2rem;
				opacity: .7;
				width: 6rem;
				@include media-breakpoint-down(sm)
				{
					top: 1rem;
					right: 1rem;
					width: 25%;
				}
			}
		}
	}
}



